import { Component, Input, OnInit } from '@angular/core';
import { RBACINFO } from './pics-page-header/@core/urls/page-header-url.config';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-page-header/@core/permissions/permission.store';
import { DataStoreService } from './pics-page-header/@core/service/data-store.service';

@Component({
  selector: 'lib-page-header',
  template: `<lib-header></lib-header>`,
  styles: []
})
export class PageHeaderComponent implements OnInit{

  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public headerEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {  }
  ngOnInit(): void {
    this.headerEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
      this._storeservice.setData('HTTPSERVICE',val.HTTPSERVICE);
      this._storeservice.setData('AUTHSERVICE',val.AUTHSERVICE);
      this._storeservice.setData('APPSERVICE',val.APPSERVICE);
      this._storeservice.setData('PAGESSERVICE', val.PAGESSERVICE)
    })
  }
}
